<template>
  <div style="width:100%">
    <div class="detail">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>

import http from "../../../../utils/http";
import api from "../../../../utils/api";
export default {
  data() {
    return {
      isHas: false
    };
  },
  created() {
    this.initData();
  },
  methods: {
    initData: async function() {
      let barList = [];
      const res = await http.get(api.getTeachingProgramNameList, { Token: http.getToken() });
      if (res.Result) {
        res.Datas.forEach((element, index) => {
          if (index == 0) this.tpid = element.TPID;
          barList.push({
            value: element.TPID,
            label: element.TPName
          });
        });
      }

      if (barList.length <= 0) {
        this.isHas = false;
      } else {
        this.isHas = true;
      }
    }
  }
};
</script>
<style lang="less" scoped>
.detail {
  width: 100%;
  margin: 0 auto;
}
</style>



